import { Divider } from "@mui/material";
import React, { useEffect, useState } from "react";
import Banner from "./Banner";
import "./home.css";
import Slide from "./Slide";

const MainComp = () => {
  const [dealProduct, setDealProduct] = useState([]);

  useEffect(() => {
    getProducts();
  }, []);

  const getProducts = async () => {
    const prod = await fetch("http://localhost:8000/slideProducts");
    const result = await prod.json();
    setDealProduct(result);
  };

  //   console.log(products);

  return (
    <div className="home_section">
      <div className="banner_part">
        <Banner />
      </div>
      <div className="slide_part">
        <div className="left_slide">
          <Slide title="Deal Of The Day" dealProduct={dealProduct}/>
        </div>
        <div className="right_slide">
          <h4>Festive latest launches</h4>
          <img
            src="https://images-eu.ssl-images-amazon.com/images/G/31/img21/Wireless/Jupiter/Launches/T3/DesktopGateway_CategoryCard2x_758X608_T3._SY608_CB639883570_.jpg"
            alt="rightimg"
          />
          <a href="www.google.com">see more</a>
        </div>
      </div>

      <Slide title="Today's Deal" dealProduct={dealProduct}/>

      <div className="center_img">
        <img
          src="https://m.media-amazon.com/images/G/31/AMS/IN/970X250-_desktop_banner.jpg"
          alt="#"
        />
      </div>

      <Slide title="Best Seller" dealProduct={dealProduct}/>
      <Slide title="Upto 80% off" dealProduct={dealProduct}/>
      <Divider />
      
    </div>
  );
};

export default MainComp;
